<template>
  <div>
    <div class="aboutus-discription">
      <div class="aboutus-discription-title">
        <span></span>
        <hr style="border: none;height: .6vh;background:-webkit-linear-gradient(left,#5DD9F4,#6256EB);width: 3vw;"/>
      </div>
      <div class="aboutus-discription-text">
        <div class="aboutus-discription-text-one">
          <span></span><br>
          <span></span><br>
          <span></span><br>
        </div>
        <div class="aboutus-discription-text-two">
          <div class="aboutus-discription-text-two-l">
            <span>APU400Hz20172016</span>
          </div>
          <div class="aboutus-discription-text-two-r">
            <span><span style="font-weight: bold"></span></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<style lang="less" scoped>
  .aboutus-discription {
    position: relative;
    top: 9vh;
    height: 90vh;
    padding-bottom: 5vh;
    font-family: ;

    .aboutus-discription-title {
      width: 100%;
      font-size: .2rem;
      color: #404040;
    }

    .aboutus-discription-text-one span {
      padding: 1vh 0;
      line-height: 7vh;
      font-size: .15rem;
      /*font-style: oblique;*/
      color: #404040;
    }

    .aboutus-discription-text-two {
      position: relative;
      top: 7vh;
      width: 86%;
      left: 7%;
      .aboutus-discription-text-two-l{
        position: relative;
        float: left;
        left: 0;
        background: url("../static/img/aboutus-discription-l.png");
        background-repeat: no-repeat;
        background-position: center 0;
        background-size: cover;
        width: 50%;
        height: 230px;
        border-radius: 30px;
        padding: 80px 100px;
        text-align: justify;
        span{
          color: #fff;
          line-height: 40px;
        }
      }
      .aboutus-discription-text-two-r{
        position: relative;
        float: right;
        background: url("../static/img/aboutus-discription-r.png");
        background-repeat: no-repeat;
        background-position: center 0;
        background-size: cover;
        width: 21%;
        height: 230px;
        border-radius: 30px;
        padding: 80px 100px;
        text-align: justify;
        span{
          color: #fff;
          line-height: 40px;
        }
      }
    }
  }
</style>

<script>
  export default {
    name: "aboutus-discription"
  }
</script>



