<template>
  <div>
    <div class="aboutus-history">
      <div class="aboutus-history-title">
        <span></span>
        <hr style="border: none;height: .6vh;background:-webkit-linear-gradient(left,#5DD9F4,#6256EB);width: 3vw;"/>
      </div>
      <div class="aboutus-history-timeaxis">
        <div class="aboutus-history-timeaxis-discription">
          <div class="aboutus-history-timeaxis-dis-u">
            <span>,,</span>
          </div>
          <div class="aboutus-history-timeaxis-year-u">2016</div>
        </div>
        <div class="aboutus-history-timeaxis-discription">
          <div class="aboutus-history-timeaxis-year-d">2017</div>
          <div class="aboutus-history-timeaxis-dis-d">
            <span>,,</span>
          </div>
        </div>
        <div class="aboutus-history-timeaxis-discription">
          <div class="aboutus-history-timeaxis-dis-u">
            <span>,,</span>
          </div>
          <div class="aboutus-history-timeaxis-year-u">2018</div>
        </div>
        <div class="aboutus-history-timeaxis-discription">
          <div class="aboutus-history-timeaxis-year-d">2019</div>
          <div class="aboutus-history-timeaxis-dis-d">
            <span>,,</span>
          </div>
        </div>
        <div class="aboutus-history-timeaxis-discription">
          <div class="aboutus-history-timeaxis-dis-u">
            <span>,,</span>
          </div>
          <div class="aboutus-history-timeaxis-year-u"></div>
        </div>
        <div class="aboutus-history-timeaxis-pic">
          <img src="../static/img/history.png" alt=""/>
        </div>
      </div>
    </div>
  </div>

</template>

<style lang="less" scoped>
  .aboutus-history {
    position: relative;
    /*top: 5vh;*/
    height: 69vh;
    padding-bottom: 5vh;
    font-family: ;

    .aboutus-history-title {
      width: 100%;
      font-size: .2rem;
      color: #404040;
    }

    .aboutus-history-timeaxis {
      position: relative;
      width: 86%;
      left: 7%;
      top: 15vh;
      .aboutus-history-timeaxis-discription{
        width: 19%;
        position: relative;
        float: left;
        left: 1.5%;
      }
      img {
        width: 100%;
        position: relative;
      }

      .aboutus-history-timeaxis-year-u,
      .aboutus-history-timeaxis-dis-u,
      .aboutus-history-timeaxis-year-d,
      .aboutus-history-timeaxis-dis-d {
        position: relative;
        padding: 0 0 0 9%;
      }
      .aboutus-history-timeaxis-year-u{
        top: 7vh;
        font-weight: bold;
        font-size: .13rem;
      }
      .aboutus-history-timeaxis-dis-u{
        float: left;
        text-align: justify;
        font-size: .10rem;
      }
      .aboutus-history-timeaxis-dis-d{
        top: 18vh;
        float: left;
        text-align: justify;
        font-size: .10rem;
      }
      .aboutus-history-timeaxis-year-d{
        top: 13vh;
        font-weight: bold;
        font-size: .13rem;
      }
    }
  }

</style>

<script>
  export default {
    name: "aboutus-history"
  }
</script>




