<template>
<div>
  <div class="main-aboutus">
    <div class="main-aboutus-text">
      <span></span><br>
      <span></span><br>
      <span></span><br>
    </div>
    <div class="main-aboutus-pic">
      <div class="pic-1">
        <img src="../static/img/main-img-aboutus.png" alt="" @click="aboutus()">
      </div>
      <div class="pic-2">
        <img src="../static/img/main-img-company-environment.png" alt="" @click="aboutus_offenvironment()">
      </div>
    </div>
  </div>
</div>
</template>

<style scoped>

  .main-aboutus-pic img:hover{
    transform: scale(1.2);
  }

  .main-aboutus-pic img{
    width: 100%;
    float: left;
    cursor: pointer;
    transition: all 0.6s;
  }
  .pic-1,.pic-2{
    width: 40%;
    height: 100%;
    float: left;
    padding: 0 5% 5vh 5%;
  }
  .main-aboutus-pic{
    padding: 12vh 5vw;
  }
  .main-aboutus-text span{
    padding: 1vh 0;
    line-height: 7vh;
    font-size: .15rem;
    /*font-style: oblique;*/
    color: #404040;
  }
  .main-aboutus{
    padding: 15vh 0;
    font-family: ;
    height: 72vh;
  }

</style>

<script>
    export default {
        name: "main-aboutus",
      methods:{
          aboutus()
          {
            const aboutus = this;
            aboutus.$router.push({name: "AboutAEME"})
          },
        aboutus_offenvironment()
        {
          const offenvironment = this;
          offenvironment.$router.push({name: "AboutAEME"})
        }
        }
    }
</script>





